@import url(https://fonts.googleapis.com/css2?family=Berkshire+Swash&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Patrick+Hand+SC&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{line-height:1.15;-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%}body{margin:0}article,aside,footer,header,nav,section{display:block}h1{font-size:2em;margin:.67em 0}figcaption,figure,main{display:block}figure{margin:1em 40px}hr{box-sizing:content-box;height:0;overflow:visible}pre{font-family:monospace,monospace;font-size:1em}a{background-color:transparent;-webkit-text-decoration-skip:objects}abbr[title]{border-bottom:none;text-decoration:underline;-webkit-text-decoration:underline dotted;text-decoration:underline dotted}b,strong{font-weight:inherit}b,strong{font-weight:bolder}code,kbd,samp{font-family:monospace,monospace;font-size:1em}dfn{font-style:italic}mark{background-color:#ff0;color:#000}small{font-size:80%}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline}sub{bottom:-.25em}sup{top:-.5em}audio,video{display:inline-block}audio:not([controls]){display:none;height:0}img{border-style:none}svg:not(:root){overflow:hidden}button,input,optgroup,select,textarea{font-family:sans-serif;font-size:100%;line-height:1.15;margin:0}button,input{overflow:visible}button,select{text-transform:none}button,html [type=button],[type=reset],[type=submit]{-webkit-appearance:button}button::-moz-focus-inner,[type=button]::-moz-focus-inner,[type=reset]::-moz-focus-inner,[type=submit]::-moz-focus-inner{border-style:none;padding:0}button:-moz-focusring,[type=button]:-moz-focusring,[type=reset]:-moz-focusring,[type=submit]:-moz-focusring{outline:1px dotted ButtonText}fieldset{padding:.35em .75em .625em}legend{box-sizing:border-box;color:inherit;display:table;max-width:100%;padding:0;white-space:normal}progress{display:inline-block;vertical-align:baseline}textarea{overflow:auto}[type=checkbox],[type=radio]{box-sizing:border-box;padding:0}[type=number]::-webkit-inner-spin-button,[type=number]::-webkit-outer-spin-button{height:auto}[type=search]{-webkit-appearance:textfield;outline-offset:-2px}[type=search]::-webkit-search-cancel-button,[type=search]::-webkit-search-decoration{-webkit-appearance:none}::-webkit-file-upload-button{-webkit-appearance:button;font:inherit}details,menu{display:block}summary{display:list-item}canvas{display:inline-block}template{display:none}[hidden]{display:none}.clearfix:after{visibility:hidden;display:block;font-size:0;content:'';clear:both;height:0}html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}.sr-only{position:absolute;width:1px;height:1px;margin:-1px;border:0;padding:0;white-space:nowrap;-webkit-clip-path:inset(100%);clip-path:inset(100%);clip:rect(0 0 0 0);overflow:hidden}html{font-size:62.5%}main{width:100%;text-align:center;background-color:#FFFCF9;font-family:'Patrick Hand SC', cursive;font-size:2rem}h1,h3,h4,span{font-family:'Berkshire Swash', cursive;margin:0}span{display:block;font-size:2.5rem;line-height:4rem;color:#EF476F}.recipeInfo p span{display:inline;font-size:2rem}h1,.bigIcon{font-size:5rem;line-height:6rem;padding-bottom:10px}h3{font-size:3rem}img{display:block;width:100%}header{height:100vh}.wrapper{max-width:90%;margin:0 auto}nav{width:100vw;height:5vh;position:fixed;bottom:0;background-color:#EF476F}.element{background-color:#EF476F;width:100vw;height:5vh;position:fixed;top:0}section{display:flex;justify-content:center;align-items:center}.smallIcon{margin:0 10px}header{display:flex;flex-direction:column;justify-content:center;align-items:center}.introText{width:50%;line-height:3.2rem}.scrollPrompt{margin-top:75px}.recipeEntry{height:100vh;display:flex;flex-direction:column;flex-wrap:wrap;align-items:center;margin:100px auto}.recipeEntry .question{margin:10px auto}.recipeEntry input,.recipeEntry textarea,.recipeEntry select,.recipeEntry option{font-family:'Patrick Hand SC', cursive;font-size:1.5rem;margin:0 10px}.recipeEntry textarea{width:100%}ul.recipe{width:100%;display:flex;flex-wrap:wrap;margin:0 auto;padding:0;margin-bottom:50px}.recipe li{width:calc(50% - 20px);display:flex;flex-wrap:wrap;margin:10px;text-align:left}.recipeImage{width:40%}.recipeInfo{width:calc(60% - 50px);border:5px solid black;padding:20px}.recipeInfo p{margin:10px 0}.recipeImage img{margin-left:0;border-top-left-radius:20px;border-bottom-left-radius:20px}button,.button{background-color:#FFD166;color:black;margin:10px;padding:5px 10px;font-size:2rem;font-family:'Patrick Hand SC', cursive}button{text-decoration:underline}button:hover,button:focus,.button:hover,button:focus{background-color:black;color:#FFD166}.likes{text-decoration:none}@media (max-width: 960px){.recipe li{flex-direction:column}.recipeInfo{width:calc(100% - 50px)}.recipeImage{width:100%}.recipeImage img{border-top-left-radius:20px;border-bottom-left-radius:0;border-top-right-radius:20px}}@media (max-width: 600px){ul.recipe{flex-direction:column}.recipe li{width:calc(100% - 20px)}.introText{width:50%;line-height:2.2rem}.introText h2{font-size:2rem}.introText span{font-size:1.8rem;line-height:2.2rem}.introText{width:100%}}

